@use "../global/variable" as *;
@use "../global/mixin" as *;

.p-about{
    background: #f4f4f4;
    position: relative;
    z-index: 10;
    padding: 30px clamp(1rem, 0.417rem + 1.22vw, 1.875rem);
    width: calc(100vw - 50px);
    &__inner{
        max-width: 1200px;
        margin-inline: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 4;
    }
    &__bg{
        position: absolute;
        z-index: 3;
        top: 30%;
        left: 0%;
            width: 100%;
            height: 214px;
            background-repeat: repeat-x;
            background-image: url(../img/loop-logo.png);
            background-size: 1907px 214px;
            animation: loop 30s linear infinite;
    }
    &__img{
        width: 42%;
    }
    &__box{
        width: 42%;
    }
    &__ttl{
        font-size:clamp(1.5rem, 1.167rem + 0.69vw, 2rem);
        margin-bottom: 0.5rem;
    }
    &__txt{
        font-size: clamp(0.875rem, 0.708rem + 0.35vw, 1.125rem);
    }
}